import React, { Component } from 'react'

export default class NewsItem extends Component {
    
      
    


    render() {
        let { title, description, imageUrl, newsUrl, author, date,source } = this.props
        return (
            <div className='my-4'>
                <div className="card" style={{ width: "18rem" }} >
                    <img src={!imageUrl?"https://www.sciencealert.com/images/2022-01/processed/earth-cross-section-illustration_1024.jpg":imageUrl} className="card-img-top" alt="..." />
                    <div className="card-body">
                        <h5 className="card-title">{title}<span className="position-absolute top-0 start-100 translate-middle badge rounded-pill bg-danger">{source}</span></h5>
                        <p className="card-text">{description}</p>
                        <p className="card-text"><small className="text-muted">By {!author?"unknown": author} on { new Date(date).toGMTString()}  </small></p>
                        <a href={newsUrl} target="_blank" rel="noreferrer" className="btn btn-sm btn-dark">Read More</a>
                    </div>
                </div>

            </div>
        )
    }

}   

